import tuiRate from "@/components/rate/rate.vue"
export default {
	data() {
		return {
			downOption: {
				use:false,
				auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
			},
			upOption: {
				use:false,
				auto: false
			},
			
			detail_data: {},
			personinfo: {},
		};
	},
	components: {
		// sunuiStar
		tuiRate
	},
	computed: {
		c_CustomBar() {
			return this.CustomBar;
		}
		
	},
	onLoad(option) {
		this.personinfo = JSON.parse(decodeURIComponent(option.item));
		uni.$on('refreshList', () => {
			console.log('refreshList');
			this.init();
		});
		this.init();
	},
	onUnload() {
		uni.$off('refreshJwt');
	},
	methods: {
		init() {
			this.$http
				.get('personwx.getparams/1.0/')
				.then(res => {
					console.log(res);
					if (res.data.code == 0) {
						this.detail_data = res.data.data;
						this.detail_data.forEach(item =>{
							this.$set(item,'rate_cur',0)
							this.$set(item,'score',0)
							item.subList.forEach(sub =>{
								this.$set(sub,'rate_cur',0)
								this.$set(sub,'score',0)
							})
						})
					}
				});
		},
		downCallback(mescroll) {
			// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			mescroll.resetUpScroll();
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		async upCallback(mescroll) {
			//联网加载数据
			await this.init()
			mescroll.endErr();
			// setTimeout(() => {
			// 	mescroll.endErr();
			// }, 1000);
		},
		deliveryPeriod(time) {
			return this.$utils.format_date(time);
		},
		avatarUrl(avatarUrl) {
			return `url(${avatarUrl})`;
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		
		change_rate_cur(e,item){
			item.rate_cur = e.index
			item.score = e.score
		},
		submit_rate(){
			let rateDate = []
			this.detail_data.forEach(item =>{
				let obj = {
					id:item.id,
					value:this.$_.add(item.rate_cur,item.score)*2,
					subList:[]
				}
				item.subList.forEach(sub =>{
					obj.subList.push({
						id:sub.id,
						value:this.$_.add(sub.rate_cur,sub.score)*2,
					})
				})
				rateDate.push(obj)
				
			})
			this.$http.get('personwx.commentperson/1.0/',{
				rateDate:JSON.stringify(rateDate),
				pid:this.personinfo.pid,
				proId:this.personinfo.proId
			}).then(res => {
				console.log(res)
				if (res.data.code == 0) {
					uni.showToast({
					    title: '提交成功',
						icon:'success',
					    duration: 2000
					});
					uni.$emit('finshRate')
					setTimeout(() =>{
						uni.navigateBack({
							delta: 1
						});
					},400)
					
				}
			});
			
		}
	}
};
